import { Sequelize, DataTypes } from 'sequelize'
import moment from 'moment'
import sequelize from './sequelize'

// 招聘信息
const ZhaopinxinxiModel = sequelize.define('ZhaopinxinxiModel', {
	id: {
		type: DataTypes.BIGINT,
		primaryKey: true,
		autoIncrement: true,
		allowNull: false,
		comment: '主键id'
	},
	gangweimingcheng: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '岗位名称'
	},
	tupian: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '图片'
	},
	zhiweileibie: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '职位类别'
	},
	zhaopinrenshu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '招聘人数'
	},
	gongzidaiyu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '工资待遇'
	},
	shangbanshijian: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '上班时间'
	},
	xueliyaoqiu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '学历要求'
	},
	nianlingyaoqiu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '年龄要求'
	},
	gongzuoxiangqing: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '工作详情'
	},
	fabushijian: {
		type: DataTypes.DATEONLY,
		allowNull: true,
		get() {
            return moment(this.getDataValue('fabushijian')).format('YYYY-MM-DD')
        },
		comment: '发布时间'
	},
	qiyezhanghao: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '企业账号'
	},
	qiyemingcheng: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '企业名称'
	},
	lianxiren: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '联系人'
	},
	lianxidianhua: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '联系电话'
	},
	qiyedizhi: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '企业地址'
	},
	clicktime: {
		type: DataTypes.DATE,
		allowNull: true,
		get() {
            return moment(this.getDataValue('clicktime')).format('YYYY-MM-DD HH:mm:ss')
        },
		comment: '最近点击时间'
	},
	addtime: {
  		type: DataTypes.DATE,
  		defaultValue: DataTypes.NOW,
    	allowNull: false,
    	get() {
            return moment(this.getDataValue('addtime')).format('YYYY-MM-DD HH:mm:ss')
        },
		comment: '添加时间'
	}
}, {
	timestamps: false,
	freezeTableName: true,
	tableName: 'zhaopinxinxi'
})

export default ZhaopinxinxiModel
